<template>
  <div>
    <van-nav-bar :title="title" left-arrow @click-left="onClickLeft" style="position: fixed; z-index: 2; width: 100%;" @click-right="showRight = true">
      <template #right>
        <van-icon v-if="rightList.length > 0" name="weapp-nav" size="20" color="#000" />
      </template>
    </van-nav-bar>
    <div :style="`min-height:${scrollHeight}px;overflow:auto; padding-top: 46px;`">
      <router-view class="page-container" ref="pageChild" @clickRightPage="onClickRightPage" />
    </div>
    <van-action-sheet v-model="showRight" :actions="rightList" cancel-text="取消" close-on-click-action @cancel="showRight = false" @select="onClickRight" />
  </div>
</template>

<script>
export default {
  name: 'PageView',
  data() {
    return {
      title: '',
      scrollHeight: window.innerHeight - 46,
      rightList: [],
      showRight: false
    }
  },
  mounted() {
    this.title = this.$route.meta.title
  },
  watch: {
    '$route'(to, form) {
      this.title = to.meta.title
    }
  },
  methods: {
    onClickLeft() {
      this.rightList = []
      this.$router.back()
    },
    onClickRight(item) {
      const pageChild = this.$refs.pageChild
      if (pageChild && typeof pageChild.selectControls === 'function') {
        pageChild.selectControls(item)
      }
    },
    onClickRightPage(data) {
      this.rightList = data
    }
  }
}
</script>

<style scoped>
.page-container {
  width: 100%;
  height: 100%;
}
</style>
